import {CommonModule} from '@angular/common';
import {ModuleWithProviders, NgModule} from '@angular/core';
import {AdaptTranslateService, DEFAULT_LANGUAGE} from './translate-service';

@NgModule({
  imports: [
    CommonModule
  ]
})

export class AdaptTranslateModule {
  static forRoot(config: AdaptTranslateModuleConfig = {}): ModuleWithProviders<AdaptTranslateModuleConfig> {
    return {
      ngModule: AdaptTranslateModule,
      providers: [
        {provide: DEFAULT_LANGUAGE, useValue: config.defaultLanguage},
        AdaptTranslateService
      ]
    };
  }
}

